<template>
    <el-container>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="types"
                v-loading="listLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="name" label="名称"></el-table-column>
                <el-table-column label="状态">
                    <template slot-scope="scope">
                        <el-tag
                            v-if="scope.row.Status == 0"
                            type="success"
                            disable-transitions
                            >正常</el-tag
                        ><el-tag v-else type="info" disable-transitions
                            >停用</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column prop="sort" label="排序"> </el-table-column>
                <el-table-column prop="operator" label="操作者">
                </el-table-column>
                <el-table-column prop="operatedate" label="编辑日期">
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="名称" prop="name">
                                <el-input
                                    v-model="editForm.name"
                                    auto-complete="off"
                                    maxlength="10"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="排序" prop="sort">
                                <el-input
                                    v-model="editForm.sort"
                                    auto-complete="off"
                                    maxlength="50"
                                    type="number"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row class="form-button">
                        <el-col :span="24">
                            <el-button @click.native="editFormVisible = false"
                                >取消</el-button
                            >
                            <el-button
                                type="primary"
                                @click.native="editSubmit"
                                :loading="editLoading"
                                >提交</el-button
                            >
                        </el-col>
                    </el-row>
                </el-form>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getTenantTypeList,
    addTenantType,
    editTenantType,
    deleteTenantType,
} from "../../api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import util from "../../../util/date";
export default {
    components: { Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            types: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                name: [
                    {
                        required: true,
                        message: "请填写名称",
                        trigger: "blur",
                    },
                ],
                sort: [
                    {
                        required: true,
                        message: "请填写排序",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            isEdit: false,
        };
    },
    methods: {
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getTenantTypeList(paras).then((res) => {
                if (res.success) {
                    this.types = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        dataFormat(row, column) {
            let data = row[column.property];
            if (data === null) {
                return "";
            }
            return util.formatDate.format(
                new Date(data.replace(/-/g, "/")),
                "yyyy-MM-dd"
            );
        },

        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.TTID };
                    deleteTenantType(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.isEdit = true;

            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.isEdit = false;
            this.editForm = {};
            this.editFormVisible = true;
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    if (this.isEdit) {
                        editTenantType(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "修改成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.editLoading = false;
                                this.getPageList();
                            } else {
                                this.editLoading = false;
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }
                        });
                    } else {
                        addTenantType(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "新增成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.editLoading = false;
                                this.getPageList();
                            } else {
                                this.editLoading = false;
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }
                        });
                    }
                }
            });
        },
    },
    mounted() {
        this.getPageList();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
</style>


